/*------------------------------------*
	@COMPONENTS #THUMBS
\*------------------------------------*/

.thumbs-wrap {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin: -10px;

    &:not(:first-child) {
        margin-top: 15px;
    }

    &:not(:last-child) {
        margin-bottom: 30px;
    }

    > span {
        margin: 10px;

        img {
            display: inline-block;
        }
    }

    .container--sm & {
        margin-bottom: 20px;
    }

    .container--sm p + & {
        margin-top: -5px;
    }

    @media (min-width: $screen-xs + 1) {
        flex-wrap: nowrap;

        &:not(:first-child) {
            margin-top: 20px;
        }

        &:not(:last-child) {
            margin-bottom: 40px;
        }

        .container--sm & {
            justify-content: center;

            &:not(:last-child) {
                margin-bottom: 20px;
            }

            > span {
                width: 90px;
            }
        }
    }

    @media (min-width: $screen-sm + 1) {
        margin: -15px;
        
        > span {
            margin: 15px !important;
        }

        &:not(:last-child) {
            margin-bottom: 45px;

            .container--sm & {
                margin-top: 20px;
                margin-bottom: 20px;
            }

            .container--sm p + & {
                margin-top: -10px;
            }
        }
    }

    @media (max-width: $screen-sm) {
        .container--sm & {
            justify-content: space-between;
        }
    }
    
    @media (max-width: $screen-xs) {
        > span {
            width: calc(100%/3 - 20px);

            &:nth-child(3n+1) {
                text-align: left;
            }
            &:nth-child(3n+3) {
                text-align: right;
            }
        }
    }
}