/*------------------------------------*
	@COMPONENTS #TITLE
\*------------------------------------*/

.title {
    font-size: 34px;
    line-height: 1.3;
    font-weight: 700;
    margin-top: 0;
    max-width: 580px;
    padding-right: 20px;
    padding-left: 20px;

    margin-right: auto;
    margin-left: auto;

    &:not(:last-child) {
        margin-bottom: 35px;

        + p {
            margin-top: -20px;
        }
    }

    &--sm {
        font-size: 28px;
        line-height: 1.3;
        
        &:not(.title--wide) {
            max-width: 380px !important;
        }
    }

    &--xs {
        font-size: 18px;
        line-height: 1.4;
    }
    
    &--xxs {
        padding: 0;
        font-size: 14px;
        line-height: 1.4;
        font-weight: 600;
        
        &:not(:last-child) {
            margin-bottom: 7px !important;
        }
    }

    &--underline,
    &--overline {
        display: flex;
        flex-direction: column;
        align-items: center;
        max-width: 540px;

        &::after {
            content: '';
            width: 50px;
            height: 3px;
            display: block;
            background-color: $red;
        }
    }

    &--overline {
        padding: 0;

        &::after {
            margin-bottom: 15px;
            order: -1;
        }
    }

    &--underline {
        margin-bottom: 0 !important;

        &::after {
            margin-top: 15px;
            margin-bottom: 25px;
        }

        .block & {
            align-items: flex-start;

            &:nth-child(even) & {
                @media (min-width: $screen-xs + 1) {
                    align-items: flex-end;
                }
            }
        }
    }

    @media (min-width: $screen-xs + 1) {
        &--wide {
            max-width: 620px;

            &.title--overline {
                max-width: 540px;
            }
        }
    }
    
    @media (max-width: $screen-sm) {
        font-size: 30px;

        &:not(:last-child) {
            margin-bottom: 20px;

            + p {
                margin-top: -10px;
            }
        }

        &--sm {
            font-size: 24px;
        }

        &--xs {
            font-size: 18px;
        }

        &--xxs {
            font-size: 14px !important;
        }

        &--overline {
            &::after {
                margin-bottom: 15px;
            }
        }

        &--underline {
            &::after {
                margin-top: 15px;
                margin-bottom: 20px;
            }
        }

        .container--shrink & {
            max-width: 480px;
        }
    }
    
    @media (max-width: $screen-xs) {
        font-size: 26px;

        &:not(.title--padded) {
            padding: 0;
        }

        br {
            display: none;
        }

        &:not(:last-child) {
            margin-bottom: 20px;

            + p {
                margin-top: -5px;
            }
        }

        &--sm {
            max-width: 340px;
            font-size: 20px;
        }

        &--xs {
            font-size: 16px;
        }

        &--overline {
            padding-right: 20px;
            padding-left: 20px;
        }

        &--underline {
            &::after {
                margin-top: 10px;
                margin-bottom: 15px;
            }
        }

        &--wide {
            padding: 0;
        }
    }
}

.suptitle {
    font-size: 14px;
    line-height: 1;
    margin-bottom: 10px;
    font-weight: 700;
    color: $btn-grey;
    text-transform: uppercase;
    display: block;
}

.title-wrap {
    margin-bottom: 90px;
}