/*------------------------------------*
    @OBJECTS #CONTAINER
\*------------------------------------*/

.container {
    max-width: 1200px;
    padding-right: 20px;
    padding-left:  20px;
    margin-right: auto;
    margin-left:  auto;

    @media (min-width: $screen-xs + 1) {
        padding-right: 40px;
        padding-left: 40px;
    }

    @media (min-width: $screen-sm + 1) {
        padding-right: 90px;
        padding-left: 90px;

        &--full {
            padding-right: 40px;
            padding-left: 40px;
        }
    }

    &--full {
        max-width: 100%;
    }

    &--flex {
        display: flex;
        flex-direction: column;
    }

    &--sm {
        max-width: 1040px;
    }
}
