/*------------------------------------*
	@COMPONENTS #BUTTONS
\*------------------------------------*/

.btn {
	display: inline-flex;
	justify-content: center;
	align-items: center;
	text-align: center;
	font-size: 13px;
	font-weight: 600;
	letter-spacing: 1.6px;
	height: 50px;
	line-height: 50px;

	& {
		min-width: 220px;
	}

	&, &:focus {
		background-color: $btn-grey;
		color: #fff;

		&:hover {
			background-color: darken($btn-grey, 10);
			color: #fff;
		}
	}

	&--mail:hover {background-color: $red;}
	&--whatsup:hover {background-color: $whatsup-clr;}
	&--facebook:hover {background-color: $facebook-clr;}
	&--twitter:hover {background-color: $twitter-clr;}

	// @media (max-width: $screen-sm) {
	// 	width: 100%;
	// }

	@media (max-width: $screen-xs) {
		.form & {
			width: 100%;
		}
	}
}

.btn--full {
	width: 100%;
}

.btn--primary {
	&, &:focus {
		background-color: $red;

		&:hover {
			background-color: $redd;
		}
	}
}

.btn--secondary {
	&, &:focus {
		background-color: $alt;

		&:hover {
			background-color: darken($alt, 10);
		}
	}
}

.btn--outline {
	&, &:focus {
		border: 1px solid $navy;
		background-color: transparent;
		color: $navy !important;

		&:hover {
			border-color: darken($navy, 10);
			background-color: $grey;
		}
	}
}

.btn--square {
	min-width: 0;
	width: 45px;
	height: 45px;
	padding: 0 !important;
}

.btn--circle {
	min-width: 0;
	width: 65px;
	height: 65px;
	border-radius: 50%;
	padding: 0 !important;
	background-color: rgba(#fff,.15) !important;
	
	.icon-play {
		position: relative;
		left: 2px;
		width: 16px;
		height: 20px;
	}
	
	&:hover {
		background-color: rgba(#fff,.25) !important;
	}
}

.btn.js-copy {
	position: relative;
	min-width: 300px;
	font-weight: 400;
	padding-right: 15px;
	padding-left: 15px;
	font-size: 14px;
	letter-spacing: 0;
	
	span {
		display: block;
		overflow: hidden;
		text-overflow: ellipsis;
		white-space: nowrap;
		text-align: left;
	}

	&::after {
		content: attr(data-copied);
		width: 100%;
		color: $navy;
		@include center;
		@include hide;
		@include trans;
	}

	&.done {
		color: transparent !important;

		&::after {
			@include show;
		}
	}

	@media (max-width: $screen-xs) {
		min-width: 0;
		max-width: 100%;
	}
}