/*------------------------------------*
	@COMPONENTS #FOOTER
\*------------------------------------*/

.footer {
	font-size: 11px;
	margin-top: 90px;
	padding-top: 30px;
	padding-bottom: 30px;
	position: relative;
	text-align: center;

	.container {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
	}

	.logo {
		margin-bottom: 15px;
	}

	@media (max-width: $screen-sm) {
		padding-top: 25px;
		padding-bottom: 25px;
		margin-top: 60px;
	}
}

	.footer__content {
		display: flex;
		align-items: center;
	}

	.footer__copy {
		font-size: inherit;
		margin-right: 6px;
		white-space: nowrap;
	}

	.footer__copy,
	.footer__links {
		@media (min-width: $screen-sm + 1) {
			flex: 1;
		}
	}

	.footer__links {
		text-align: right;
		display: flex;
		align-items: center;
		justify-content: flex-end;

		a {
			color: $text-clr;
			text-decoration: underline;

			&:not(:last-child) {
				margin-right: 6px;
			}

			&:hover {
				text-decoration: none;
			}
		}
	}

.footer--lg {
	background-color: #f0f0f0;
	padding-top: 55px;
	padding-bottom: 55px;
	margin-top: 0;
	
	p {
		color: $text-alt;
		font-size: 13px;
	}

	.logo {
		margin-bottom: 25px;
		width: 90px;
		height: 38px;
	}

	p {
		&:not(:last-child) {
			margin-bottom: 20px !important;
		}
	}

	@media (max-width: $screen-sm) {
		.logo {
			margin-bottom: 20px;
		}

		p {
			&:not(:last-child) {
				margin-bottom: 15px !important;
			}
		}
	}
	
	@media (max-width: $screen-xs) {
		.container {
			padding-right: 40px;
			padding-left: 40px;
		}
	}
	
	@media (min-width: $screen-xs + 1) {
		padding-top: 70px;
		padding-bottom: 70px;

		.container {
			max-width: 620px;
		}
	}
	@media (min-width: $screen-sm + 1) {
		padding-top: 90px;
		padding-bottom: 90px;

		.container {
			max-width: 880px;
		}
	}
}