/*------------------------------------*
	@COMPONENTS #LEAD
\*------------------------------------*/

p {
    &:not(.lead) {
        &:not(:last-child) {
            margin-bottom: 25px;

            @media (max-width: $screen-sm) {
                margin-bottom: 25px;
            }
            @media (max-width: $screen-xs) {
                margin-bottom: 20px;
            }
        }
    }

    @media (max-width: $screen-sm) {
        .container--shrink & {
            max-width: 620px;
            margin-right: auto;
            margin-left: auto;
        }
    }

    @media (max-width: $screen-xs) {
        .title ~ &:not(.lead) {
            font-size: 14px;
        }
    }
}

.lead {
    font-size: 18px;

    &--sm {
        font-size: 16px;
    }

    &--narrow {
        max-width: 380px;
    }

    &:not(:last-child) {
        margin-bottom: 25px;
    }

    + i {
        font-size: 14px;
        display: block;
    }

    @media (max-width: $screen-sm) {
        &:not(:last-child) {
            margin-bottom: 20px;
        }

        + i {
            margin-top: -5px;
        }
    }

    @media (max-width: $screen-xs) {
        font-size: 16px;

        &--narrow {
            max-width: 280px;
        }
    }
}