/*------------------------------------*
	@COMPONENTS #CAROUSEL
\*------------------------------------*/

.carousel-wrap {
    @media (max-width: $screen-xs) {
        margin-top: 75px;
    }

    @media (min-width: $screen-xs + 1) {
        position: relative;
        margin-top: 35px;

        .container--flex & {
            order: 3;
        }
    }
}

.carousel {
    @media (max-width: $screen-xs) {
        margin-top: 18px;
    }

    @media (min-width: $screen-xs + 1) {
        display: flex;
        flex-wrap: wrap;
        margin: -5px;
    }
    
    @media (min-width: $screen-sm + 1) {
        margin: -10px;
    }

    @media (max-width: $screen-xs) {
        position: relative;
    }

    .slick-dots {
        display: flex;
        @include list;
        @include centerH;
        bottom: 100%;
        margin-bottom: 12px;

        li {
            &:not(:last-child) {
                margin-right: 5px;
            }
        }

        button {
            background-color: $navy;
            color: transparent;
            font-size: 0;
            width: 30px;
            height: 3px;
            opacity: .25;
            padding: 0;
        }

        .slick-active {
            button {
                opacity: 1;
            }
        }
    }
}

    .carousel__slide {
        line-height: 0;
        outline: transparent !important;

        img {
            display: inline-block;
            max-width: 100%;
        }
        
        @media (min-width: $screen-xs + 1) {
            margin: 5px;
            width: 100%;
            max-width: calc(100%/2 - 10px);
        }
        
        @media (min-width: $screen-sm + 1) {
            margin: 10px;
            max-width: calc(100%/2 - 20px);
        }
    }