/*------------------------------------*
	@COMPONENTS #SECTION
\*------------------------------------*/

.section {
    &:not(:last-child) {
        margin-bottom: 60px;

        @media (min-width: $screen-xs + 1) {
            margin-bottom: 70px;
        }

        @media (min-width: $screen-sm + 1) {
            margin-bottom: 90px;            
        }
    }

    .container {
        text-align: center;
    }
}

    .section__wrap {
        max-width: 700px;
        margin-right: auto;
        margin-left: auto;

        display: flex;
        flex-direction: column;
        align-items: center;

        &:not(:last-child) {
            margin-bottom: 60px;

            @media (min-width: $screen-xs + 1) {
                margin-bottom: 70px;    
            }
    
            @media (min-width: $screen-sm + 1) {
                margin-bottom: 90px;
            }
        }

        @media (max-width: $screen-sm) {
            padding-right: 40px;
            padding-left: 40px;
        }
        
        @media (max-width: $screen-xs) {
            padding-right: 20px;
            padding-left: 20px;
        }

        &--shrink {
            .title {
                padding: 0;
            }
            
            @media (max-width: $screen-sm) {
                max-width: 460px;
            }

            @media (max-width: $screen-xs) {
                max-width: 280px;
            }
        }
    }

    .section__img {
        line-height: 0;
        &--lg {
            &:not(:last-child) {
                margin-bottom: 60px;

                @media (min-width: $screen-xs + 1) {
                    margin-bottom: 70px;
                }
                @media (min-width: $screen-sm + 1) {
                    margin-bottom: 90px;
                }
            }
        }
    }

.section--hero {
    margin-top: -120px;
    position: relative;

    background: {
        size: cover;
        position: center center;
    }
    
    .container {
        min-height: 700px;
        @include flexC;
        position: relative;
        z-index: 10;
    }

    &::after {
        content: '';
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        background-color: $navy;
        opacity: .8;
    }

    p {
        max-width: 380px;
    }

    .floater {
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        transform: translate(0, 0);
        background-color: transparent;
        align-items: flex-end;

        @media (min-width: $screen-xs + 1) {
            padding-bottom: 25px;
        }

        @media (min-width: $screen-sm + 1) {
            padding-bottom: 35px;
        }
    }

    @media (max-width: $screen-sm) {
        margin-top: -110px;
        
        .container {
            min-height: 600px;
        }
    }

    @media (max-width: $screen-xs) {
        margin-top: -100px;
    }
}

    .section__row {
        display: flex;
        justify-content: space-between;
        align-items: center;

        .section__img {
            flex-basis: 52.941176%;
            min-width: 52.941176%;
            line-height: 0;

            img {
                width: 100%;
            }

            @media (max-width: $screen-xs) {
                margin-bottom: 20px;
            }
        }
        .section__wrap {
            flex-basis: 37.2549%;
            min-width: 37.2549%;
            margin-left: auto;
            margin-right: 0;
            padding: 0;
        }
        
        &--reverse {
            flex-direction: row-reverse;
            
            .section__wrap {
                margin-left: 0;
                margin-right: auto;
            }
        }

        @media (min-width: $screen-xs + 1) {
            .title {
                padding: 0;
            }
        }
        
        @media (max-width: $screen-sm) {
            font-size: 14px;
            
            .section__wrap {
                flex-basis: 42.8571%;
                min-width: 42.8571%;
            }
        }

        @media (max-width: $screen-xs) {
            flex-direction: column;

            p {
                padding-right: 20px;
                padding-left: 20px;
            }
        }
    }

    #sectionForm {
        padding-top: 60px;

        @media (min-width: $screen-xs + 1) {
            padding-top: 70px;
        }
        @media (min-width: $screen-sm + 1) {
            padding-top: 90px;
        }
    }

.section--negative {
    background-color: $navy;
    color: #fff;
    
    .title {
        color: #fff;
    }

    @media (max-width: $screen-sm) {
        .container {
            padding: 50px 45px;
        }
    }

    @media (max-height: 799px) {
        .container {
            padding-top: 50px;
            padding-bottom: 50px;
        }
    }
}