.fp-slidesNav{
    position: absolute;
    z-index: 4;
    opacity: 1;
    transform: translate3d(0,0,0);
    left: 0 !important;
    right: 0;
    margin: 0 auto !important;
    
    &.fp-bottom {
        bottom: 17px;
    }
    &.fp-top {
        top: 17px;
    }

    ul {
        li {
            display: inline-block;
        }
    }
}

