.fp-auto-height {
    &.fp-section,
    .fp-slide,
    .fp-tableCell{
        height: auto !important;
    }
}

.fp-responsive {
    .fp-auto-height-responsive {
        &.fp-section,
        .fp-slide,
        .fp-tableCell {
            height: auto !important;
        }
    }
}

/*Only display content to screen readers*/
.fp-sr-only{
    position: absolute;
    width: 1px;
    height: 1px;
    padding: 0;
    overflow: hidden;
    clip: rect(0, 0, 0, 0);
    white-space: nowrap;
    border: 0;
}