/*------------------------------------*
	@COMPONENTS #FORMS
\*------------------------------------*/

.form {
    width: 100%;
    max-width: 380px;
}

.form__input {
    color: $form-clr;
    border-color: transparent;
    background-color: transparent;
    font-family: $base-font;
    font-size: 14px;
    border: 1px solid $border;
    border-radius: 0 !important;
    padding: 0 15px;
    height: 45px;

    &::-webkit-input-placeholder {
       color: $placeholder-clr;
    }
    &::-moz-placeholder {
        color: $placeholder-clr;
        opacity: 1;  
    }
    &:-ms-input-placeholder {
        color: $placeholder-clr;  
    }
    &:not(textarea) {
        line-height: 38px;
    }

    &--full {
        width: 100%;
    }

    &:hover,
    &:focus {
        // border-color: $primary;
    }
}

.form__input--negative {
    border-color: #fff;
    color: #fff !important;

    &::-webkit-input-placeholder {
       color: rgba(#fff,.6);
    }
    &::-moz-placeholder {
        color: rgba(#fff,.6);
    }
    &:-ms-input-placeholder {
        color: rgba(#fff,.6);  
    }
}

textarea.form__input {
    resize: none;
}

.form__group {
    margin-bottom: 30px;

    + .form__group {
        margin-top: -15px;
    }

    @media (max-width: $screen-sm) {
        margin-bottom: 25px;

        + .form__group {
            margin-top: -10px;
        }
    }

    @media (max-width: $screen-sm) {
        + .form__group {
            margin-top: -15px;
        }
    }

    // &--sm {
    //     margin-bottom: 15px;

    //     + .btn,
    //     + .btn-group {
    //         margin-top: 15px;
    //     }
    // }
}