/*------------------------------------*
	@COMPONENTS #LOADER
\*------------------------------------*/
.page--full {
    &:not(.loaded) {
        overflow: hidden;
    }
}

.loader {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background-color: #fff;
    z-index: 2000;
    @include trans;

    @include flexC;

    .logo-mark {
        animation: pulse 2s ease-in-out infinite;
    }

    .loaded & {
        @include hide;
    }
}

@keyframes pulse {
	0% {
		transform: scale(.7);
	}

	50% {
		transform: scale(1.2);
	}

	100% {
		transform: scale(.7);
	}
}
