/*------------------------------------*
	@COMPONENTS #BLOCKS
\*------------------------------------*/

.blocks-wrap {
    display: flex;
    flex-wrap: wrap;
    padding-top: 10px;
}

    .block {
        font-size: 14px;
        
        &:not(:last-child) {
            margin-bottom: 50px;
        }
        
        img {
            max-width: 100%;
            margin-bottom: 15px;
        }

        .title {
            margin-right: 0;
            margin-left: 0;
            padding: 0;

            &--underline {
                max-width: 100% !important;
            }
        }
        
        @media (max-width: $screen-xs) {
            > *:not(img) {
                padding-left: 20px;
                padding-right: 20px;
            }
            
            .title {
                max-width: 260px;
                margin-right: auto;
                margin-left: auto;
                text-align: center;
                align-items: center;

                br {
                    display: none;
                }
            }
        }

        @media (min-width: $screen-xs + 1) {
            width: calc(50% - 30px);

            .title {
                br {
                    display: block;
                }
            }

            img {
                margin-bottom: 20px;
            }
            
            &:nth-child(odd) {
                text-align: left;
                margin-right: 30px;
            }

            &:nth-child(even) {
                text-align: right;
                margin-left: 30px;

                .title--underline {
                    align-items: flex-end;
                }
            }

            &:not(:last-child) {
                margin-bottom: 60px;
            }

            &:nth-last-child(2) {
                margin-bottom: 0;
            }
        }
        
        @media (min-width: $screen-sm + 1) {
            width: calc(50% - 50px);

            .title--sm {
                font-size: 24px;
            }

            img {
                margin-bottom: 25px;
            }

            &:nth-child(odd) {
                margin-right: 50px;
            }

            &:nth-child(even) {
                margin-left: 50px;
            }
        }
    }