/*------------------------------------*
	@COMPONENTS #ALERT
\*------------------------------------*/

.alert {
    text-align: center;
    background-color: $grey;
    padding: 15px 20px;
    border-bottom: 4px solid $primary;
    font-size: 14px;
    
    &:not(:last-child) {
        margin-bottom: 15px;

        @media (min-width: $screen-xs + 1) {
            margin-bottom: 20px;
        }
    }
}