/*------------------------------------*
	@COMPONENTS #SPECS
\*------------------------------------*/

.specs-wrap {
    display: flex;
    flex-wrap: wrap;
    margin-top: 20px;
    padding-right: 20px;
    padding-left: 20px;
    margin-bottom: -5px;

    @media (max-width: $screen-xs) {
        
    }

    &:not(:last-child) {
        margin-bottom: 55px;

        @media (max-width: $screen-sm) {
            margin-bottom: 40px;    
        }
        @media (max-width: $screen-xs) {
            margin-bottom: 35px;    
        }
    }


    @media (min-width: $screen-xs + 1) {
        padding: 0;
    }
}

.specs-wrap__divider {
    min-width: 100%;

    @media (min-width: $screen-xs + 1) {
        display: none;
    }
}

.specs {
    flex: 1;
    margin-bottom: 35px;
    text-align: left;
    font-size: 13px;

    @media (max-width: $screen-sm) {
        margin-bottom: 30px;
    }

    @media (max-width: $screen-xs) {
        margin-bottom: 25px;

        &:nth-of-type(even) {
            text-align: right;
        }

        &:nth-of-type(n+5) {
            margin-bottom: 0;
        }
    }

    @media (min-width: $screen-xs + 1) {
        min-width: calc(100%/3);
        
        &:nth-of-type(3n+2) {
            text-align: center;
        }
        &:nth-of-type(3n+3) {
            text-align: right;
        }
        &:nth-of-type(n+4) {
            margin-bottom: 0;
        }
    }
}

    .specs__list {
        @include list;
    }