/*------------------------------------*
	@COMPONENTS #HEADER
\*------------------------------------*/

.header {
	margin-top: 15px;
	margin-bottom: 73px;
	position: relative;
	z-index: 100;

	.container {
		display: flex;
		justify-content: space-between;
		align-items: flex-start;
		height: 12px;
	}

	.page--landing & {
		color: #fff;
	}		

	@media (min-width: $screen-xs + 1) {
		margin-top: 25px;
	}

	@media (min-width: $screen-sm + 1) {
		margin-top: 35px;
	}
}

	.header__logo {
		color: $text-clr;
		line-height: 0;

		&:hover {
			opacity: .5;
		}

		.page--landing & {
			color: #fff;
		}	

		@media (min-width: $screen-sm + 1) {
			.page--landing & {
				top: 0;
				@include centerH;				
			}
		}
	}

	.header__text {
		text-align: right;
		font-size: 13px;
		font-weight: 600;
		line-height: 1;
		writing-mode: vertical-rl;
		position: absolute;
		white-space: nowrap;
		top: 0;
		right: 20px;

		@media (min-width: $screen-xs + 1) {
			right: 30px;
		}
		
		@media (min-width: $screen-sm + 1) {
			right: 40px;
		}
	}
