/*------------------------------------*
	@COMPONENTS #SOCIALS
\*------------------------------------*/

.socials {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    margin: -15px;

    .btn {
        line-height: 1;
        height: 45px;
    }

    .icon {
        width: 25px;
        height: 25px;
    }

    > * {
        margin: 10px !important;
    }

    @media (min-width: $screen-xs + 1) {
        margin: -6px;

        > * {
            margin: 6px !important;
        }
    }

    @media (min-width: $screen-sm + 1) {
        margin: -12.5px;

        > * {
            margin: 12.5px !important;
        }
    }
}