/*------------------------------------*
    @OBJECTS #BUTTONS
\*------------------------------------*/

button {
    cursor: pointer;
    border-color: transparent;
    @include trans;
    
    &,
    &:focus,
    &:active {
        outline: none !important;

        &:not(.btn) {
            border: none;
        }
    }
}

*[class*="btn-"]:not(.btn-wrap) {
    user-select: none;

    // font: inherit;
    font-family: $base-font;
    text-align: center;
    text-transform: none;

    margin: 0;

    cursor: pointer;
    overflow: visible;
    @include trans;

    padding: 0 25px;

    &,
    &:hover,
    &:active,
    &:focus {
        text-decoration: none;
    }
}