/*------------------------------------*
	@COMPONENTS #FLOATER
\*------------------------------------*/

.floater {
    position: fixed;
    display: flex;
    justify-content: space-between;
    align-items: center;
    right: 0;
    bottom: 0;
    left: 0;
    height: 90px;
    padding: 20px 40px;
    background-color: #fff;
    transform: translateY(500%);
    @include trans;
    z-index: 1000;

    .btn {
        @include center;
    }

    .scrolled & {
        &.show {
            transform: translateY(0);
        }
    }

    @media (max-width: $screen-sm) {
        padding: 20px 30px;
    }

    @media (max-width: $screen-xs) {
        padding: 15px 20px;

        .logo-mark,
        .floater__text {
            display: none;

            .section & {
                display: block;
            }
        }

        .btn {
            position: static;
            transform: translate(0, 0);
            width: 100%;
        }
    }
}

    .floater__text {
		font-size: 13px;
		font-weight: 600;
		line-height: 1;
    }