/*------------------------------------*
	@COMPONENTS #TABLE
\*------------------------------------*/

.table {
    border-collapse: collapse;
    border-bottom: 1px solid rgba($navy,.25);
    font-size: 14px;

    &:not(:last-child) {
        margin-bottom: 40px;
    }

    tr {
        display: flex;
    }

    th, td {
        line-height: 1.3;
        text-align: center;
        border-top: 1px solid rgba($navy,.125);
        padding: 30px 0;

        &:first-child {
            text-align: left;
        }
        
        &:last-child {
            text-align: right;
        }

        &:first-child,
        &:last-child {
            flex: 1;
        }
    }

    th {
        text-transform: uppercase;
        font-weight: 700;
        border-color: rgba($navy,.25);
    }

    td {
        color: $text-alt;
    }
}

    .table__highlight {
        td {
            border-color: $turq;
            color: $turqd;
            font-weight: 500;
        }

        + tr {
            td {
                border-color: $turq;
            }
        }
    }

.table-note {
    font-size: 14px;
    color: $text-alt;

    strong {
        color: $text-clr;
        font-weight: 500;
    }

    @media (max-width: $screen-xs) {
        padding-right: 20px;
        padding-left: 20px;
    }
}