/*------------------------------------*
	@COMPONENTS #SIDENAV
\*------------------------------------*/

.sidenav {
    position: fixed;
    top: 35px;
    left: 40px;
    z-index: 1000;

    span {
        background-color: $navy;
        width: 15px;
        height: 2px;
        display: block;
        @include trans;

        &:not(:last-child) {
            margin-bottom: 5px;
        }

        &.active {
            width: 25px;
        }
    }

    &[data-sidenav="1"] {
        span {
            background-color: #fff;
        }
    }

    @media (max-width: $screen-sm) {
        display: none;
    }
}