//------------------------------------\\
//  @SETTINGS #FONTS
//------------------------------------//

@font-face {
    font-family: 'GalanoGrotesque';
    src: local('☞Galano Grotesque SemiBold'), local('GalanoGrotesque-SemiBold'),
        url('../fonts/GalanoGrotesque-SemiBold.woff2') format('woff2'),
        url('../fonts/GalanoGrotesque-SemiBold.woff') format('woff');
    font-weight: 600;
    font-style: normal;
}

@font-face {
    font-family: 'GalanoGrotesque';
    src: local('☞Galano Grotesque Bold'), local('GalanoGrotesque-Bold'),
        url('../fonts/GalanoGrotesque-Bold.woff2') format('woff2'),
        url('../fonts/GalanoGrotesque-Bold.woff') format('woff');
    font-weight: bold;
    font-style: normal;
}

@font-face {
    font-family: 'GalanoGrotesque';
    src: local('☞Galano Grotesque'), local('GalanoGrotesque-Regular'),
        url('../fonts/GalanoGrotesque-Regular.woff2') format('woff2'),
        url('../fonts/GalanoGrotesque-Regular.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'GalanoGrotesque';
    src: local('☞Galano Grotesque Medium'), local('GalanoGrotesque-Medium'),
        url('../fonts/GalanoGrotesque-Medium.woff2') format('woff2'),
        url('../fonts/GalanoGrotesque-Medium.woff') format('woff');
    font-weight: 500;
    font-style: normal;
}

@font-face {
    font-family: 'GalanoGrotesque';
    src: local('☞Galano Grotesque Italic'), local('GalanoGrotesque-Italic'),
        url('../fonts/GalanoGrotesque-Italic.woff2') format('woff2'),
        url('../fonts/GalanoGrotesque-Italic.woff') format('woff');
    font-weight: normal;
    font-style: italic;
}

