//------------------------------------\\
//  @SETTINGS #VARIABLES
//------------------------------------//

//COLORS
$navy: #0c1e3a;
$navyl: #656A72;
$red: #e81c2c;
$redd: #e21b2b;
$grey: #eeeeee;
$turq: #00A8A5;
$turqd: #009E9B;

$primary: 					$red;
$alt: 						$navy;

$btn-grey: #BAC0C9;
$btn-primary: $primary;
$btn-secondary: $alt;

// Backgrounds
$bgr: 						#fff;
$base-bgr:                  #fff;


// Fonts
$base-font:					'GalanoGrotesque', serif !default;

$base-font-size:            16px;
$base-line-height:         	1.4;
$base-font-weight:          400;

$text-clr: 					$navy;
$text-alt: 					$navyl;
$form-clr:				    $navy;
$placeholder-clr:			$navyl;
$link-clr:					$text-alt;
$link-hover:				$text-clr;

$border:					#0C1E3A;
$borderd:					#aaa;



// Layout
$space:                     30px;
$radius:					3px;
$grid-gutter:     			20px !default;


// Breakpoints
$screen-xs:                 560px;
$screen-sm:                 800px;
$screen-md:                 992px;
$screen-lg:                 1200px;

$breakpoint:     			991px !default;

// Select Text
$selected-text-bgr:			$navy;
$selected-text-clr:			#fff;


//Bezier
$bezier: cubic-bezier(.25, .1, .25, 1);