/*------------------------------------*
	@TRUMPS #HELPERS
\*------------------------------------*/

.clean {
	margin: 0;
	padding: 0;
}

//NO TRANSITION CLASS
.no-transition {
    &,
    * {
        transition: none !important;
    }
}

//NICER AMPERSAND
.amp {
    font-family: Baskerville, Palatino, "Palatino Linotype", "Book Antiqua", serif;
    font-size: 1.4em;
    font-style: italic;
    font-weight: normal;
}

//NOWRAP
.nowrap {
    white-space: nowrap;
}

.nounderline {
    text-decoration: none !important;
}


//Display
.hide {
    display: none !important;
}

.hide-sm-up {
    @media (min-width: $screen-sm + 1) {
        display: none;
    }
}
.hide-sm-down {
    @media (max-width: $screen-sm) {
        display: none;
    }
}

.hide-lg-up {
    @media (min-width: $screen-lg + 1) {
        display: none;
    }
}
.hide-lg-down {
    @media (max-width: $screen-lg) {
        display: none;
    }
}


//Flex
.flex {
    display: flex;

    &--j-between {
        justify-content: space-between;
    }

    &--a-start {
        align-items: flex-start;
    }
    &--a-center {
        align-items: center;
    }
}

.text-left {
    text-align: left;

    &-sm {
        @media (max-width: $screen-sm) {
            text-align: left;
        }
    }
}
.text-center {
    text-align: center;
    &-sm {
        @media (max-width: $screen-sm) {
            text-align: center;
        }
    }
}
.text-right {
    text-align: right;
    &-sm {
        @media (max-width: $screen-sm) {
            text-align: right;
        }
    }
}