/*------------------------------------*
    @OBJECTS #MODAL
\*------------------------------------*/

.modal-on {
    overflow: hidden;
}

.modal {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 30000;
    overflow: auto;
    background-color: #000;
    display: none;
}

.modal--video {
    .modal__body {
        line-height: 0;

        video {
            max-width: 100%;
        }
    }
}

    .modal__wrap {
        padding: 30px;
        min-height: 100vh;
        min-height: calc(var(--vh, 1vh) * 100);
        display: flex;
        align-items: center;
        justify-content: center;

        @media (max-width: $screen-xs) {
            padding-top: 50px;
        }
    }

    .modal__content {
        max-width: 1080px;
        width: 100%;
        background-color: #212121;
        font-size: 14px;
        display: flex;
        flex-direction: column;
        @include trans;
    }

    .modal__close {
        position: absolute;
        top: 30px;
        right: 35px;
        padding: 0;
        color: #fff;
        background-color: transparent;
        line-height: 1;
        width: 14px;
        height: 14px;

        .icon {
            width: 100%;
            height: 100%;
            @include center;
        }
    }